.duck-container {
    font-size: 2rem;
    position: relative;
    display: inline-block;
    cursor: pointer;
  }
  
  .eye {
    position: absolute;
    font-size: 0.5rem;
    top: 20%;
  }
  
  .left-eye {
    left: 25%;
  }
  
  .right-eye {
    right: 25%;
  }
  
  .move {
    animation: move 0.5s ease;
  }
  
  @keyframes move {
    0% { transform: translateX(0); }
    50% { transform: translateX(10px); }
    100% { transform: translateX(0); }
  }
  