.estrela-container {
    font-size: 2rem;
    cursor: pointer;
    position: relative;
    display: inline-block;
  }
  
  .estrela-sparkle {
    position: absolute;
    font-size: 1rem;
    opacity: 1;
    animation: fade 2s ease-out forwards;
  }
  
  @keyframes fade {
    0% {
      opacity: 1;
      transform: scale(1);
    }
    100% {
      opacity: 0;
      transform: scale(0.5) translateY(-30px);
    }
  }
  