.moedas-container {
    font-size: 2rem;
    cursor: pointer;
    position: relative;
    display: inline-block;
  }
  
  .moedas-note {
    position: absolute;
    font-size: 1.5rem;
    opacity: 1;
    animation: disperse 2s ease-out forwards;
  }
  
  @keyframes disperse {
    0% {
      opacity: 1;
      transform: scale(1);
    }
    100% {
      opacity: 0;
      transform: scale(0.5) translateY(-30px);
    }
  }
  